/* ------------------  */
/*        CORE         */
/* ------------------  */

body {
    /* font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif; */
    /* font-family:'Times New Roman', Times, serif; */
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    background-color: #f0f0f0;
    margin: 0;
    padding: 0;
    background-color: #2c2a2a;
    color:white;
}

.root {
    margin: 0 auto;
    padding: 10px 20px 10px 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
    text-align: center;
    margin-bottom: 20px;
}

button {
    position: relative;
    display: inline-block;
    white-space:pre;
    cursor: pointer;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    background-color: rgba(173, 216, 230, 0);
    border: 1px solid #cccccc;
    border-radius: 4px;
    padding: 6px 12px;
    font-size: 14px;
    width:150px;
}

#training, #forge, #skills, #exploration, #essence, #achievements, #tournament, #settings, #radiance {
    display: none;
    width:100%;
}

.main-window {
    position:relative;
    display: flex;
    flex-direction:row;
    justify-content:space-around;
    flex-wrap:wrap;
    max-height:1000px;
}

.content-tab {
    display: flex;
    flex-direction: column;
}

.content-tab-column-container {
    display:flex;
    flex-direction:row;
    padding:5px;
}

.content-tab-col {
    padding:20px;
    margin-top:60px;
    display:flex;
    flex-direction:column;
}

.feature-cell{
    display:flex;
    flex-direction:row;
    align-items:center;
    height:80px;
}
.feature-cell * {
    padding-left:5px;
    padding-right:5px;
}
.feature-cell button{
    width:250px;
}

.feature-current-value {
    width:120px;
}

.feature-name {
    width:200px;
}
/* ------------------  */
/*     END CORE        */
