/*       TOOLTIPS      */
/* ------------------  */
.tooltip-text {
    visibility: hidden;
    background-color: #202128;
    border: 1px solid white;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px;

    min-width: 200px;
    white-space: pre-wrap!important;
    word-break: break-word!important;
    overflow-wrap: break-word!important;
    font-size: 10px;
    font-weight: 400;
    
    position: absolute;
    z-index: 1234234334;
    top: 100%;
    /* top: 5%; */
    left: 10%;
    
    opacity: 0;
    user-select: none;
    pointer-events: none;
}

button:hover .tooltip-text {
    visibility: visible;
    opacity: 1;
}

.stats-container:hover .tooltip-text {
    visibility: visible!important;
    opacity: 1!important;
}

.tooltips-off .tooltip-text {
    display:none!important;
}

