@keyframes fadein {
    0% {opacity: 0;} /* Invisible at the start */
    10% {opacity: 1;} /* Fully visible after 10% of the duration (0.6s) */
    90% {opacity: 1;} /* Still fully visible at 90% of the duration (5.4s) */
    100% {opacity: 0;} /* Fully invisible at the end */
}


