/* ------------------  */
/*      CHECKBOX       */
/* ------------------  */
input[type="checkbox"] {
    height: 0;
    width: 0;
    visibility: hidden;
}
  
label {
    cursor: pointer;
    width: 40px;
    height: 30px;
    background: grey;
    display: block;
    border-radius: 7px;
    position: relative;
    text-align:right;
    padding-top:10px;
    margin-left:10px;
    margin-right:10px;
    color:white;
}
  
label span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: small;
    user-select: none;
    pointer-events: none;
}
  
label:after {
    content: '';
    position: absolute;
    top: 2px;
    left: 4px;
    width: 10px;
    height: 25px;
    background: #fff;
    border-radius: 7px;
    transition: 0.1s;
    padding-top:10px;
    opacity:0.8;
}
  
input:checked + label {
    background: #34a934;
}
  
input:checked + label:after {
    left: calc(100% - 5px);
    transform: translateX(-100%);
}
  
label:active:after {
    width: 20px;
}

/* ------------------  */
/*    END CHECKBOX     */
/* ------------------  */



/* ------------------  */
/*       MODAL         */
/* ------------------  */
.modal {
    display: none;
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 400px;
    height: 400px;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
  }
  
  /* Modal Content */
  .modal-content {
    background-color: #fefefe;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
  }
  
  /* The Close Button */
  .close {
    color: #aaaaaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }
  

#modal-message{
    color:black;
    white-space:pre;
}
/* ------------------  */
/*     END MODAL       */
